import { ReactElement, useState } from "react";
import { Container } from "../../components/Layout/Layout.styled";
import {
  BtnWrapper,
  Heading,
  HeroSection,
  ToursHeading,
  StyledHeading,
  Anchor,
  Thumb,
  ToursSection,
  List,
  CarouselHandler,
  ListWrapper,
  CardCarousel,
} from "./HomePage.styled";
import { SVG } from "../../images";
import { cardImages, bgImages } from "../../images";
import { Card } from "../../components/Card/Card";
import { GET_ROCKETS } from "../../apollo/client";
import { useQuery } from "@apollo/client";
import { RocketType } from "../../types/types";
import { StyledIconButton } from "../../components/IconButton/IconButton.styled";

const HomePage: React.FC = (): ReactElement => {
  const { loading, error, data } = useQuery(GET_ROCKETS);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [curr, setCurr] = useState(0);

  const prev = () =>
    setCurr((curr) => (curr === 0 ? data.rockets.length - 3 : curr - 1));
  const next = () =>
    setCurr((curr) => (curr === data.rockets.length - 3 ? 0 : curr + 1));
  const cardCarouselHandler = (index: number) => {
    setCurr(index);
  };

  const goToSlide = (imageIndex: number) => {
    setCurrentIndex(imageIndex);
  };

  return (
    <>
      <HeroSection
        style={{
          backgroundImage: `linear-gradient(
    rgba(47, 48, 58, 0.4),
    rgba(47, 48, 58, 0.4)
  ), url(${bgImages[currentIndex]})`,
        }}
      >
        <CarouselHandler>
          {bgImages.map((image: string, imageIndex: number) => (
            <div key={imageIndex} onClick={() => goToSlide(imageIndex)}>
              {currentIndex === imageIndex ? (
                <SVG.CarouselDotFull style={{ cursor: "pointer" }} />
              ) : (
                <SVG.CarouselDot style={{ cursor: "pointer" }} />
              )}
            </div>
          ))}
        </CarouselHandler>
        <Container>
          <Heading>The space is waiting for</Heading>
          <StyledHeading>You</StyledHeading>
          <Anchor href="#tours">
            Explore tours
            <SVG.ArrowDown />
          </Anchor>
        </Container>
      </HeroSection>
      <ToursSection id="tours">
        <Container>
          <Thumb>
            <ToursHeading>Popular tours</ToursHeading>
            <BtnWrapper>
              <StyledIconButton type="button" onClick={prev}>
                <SVG.ArrowLeft />
              </StyledIconButton>
              <StyledIconButton type="button" onClick={next}>
                <SVG.ArrowRight />
              </StyledIconButton>
            </BtnWrapper>
          </Thumb>
          {error && <p>{error.message}</p>}
          {loading ? (
            <p>Loading...</p>
          ) : (
            <ListWrapper>
              <List style={{ transform: `translateX(-${curr * 33.55}%)` }}>
                {data.rockets.map((rocket: RocketType, idx: number) => (
                  <Card
                    key={rocket.id}
                    rocket={rocket}
                    image={
                      cardImages[
                        idx < cardImages.length ? idx : idx - cardImages.length
                      ]
                    }
                  />
                ))}
              </List>
              <CardCarousel
                style={{ display: "flex", justifyContent: "center" }}
              >
                {data.rockets
                  .slice(0, 2)
                  .map((rocket: RocketType, idx: number) => (
                    <div
                      key={rocket.id}
                      onClick={() => cardCarouselHandler(idx)}
                    >
                      {curr === idx ? (
                        <SVG.CarouselDotFullDark
                          style={{ cursor: "pointer" }}
                        />
                      ) : (
                        <SVG.CarouselDotDark style={{ cursor: "pointer" }} />
                      )}
                    </div>
                  ))}
              </CardCarousel>
            </ListWrapper>
          )}
        </Container>
      </ToursSection>
    </>
  );
};

export default HomePage;
